  background-color: #1789fc;
  padding: 20px;
  text-align: left;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  &-closeBtn {
    position: absolute;
    top: 12px;
    left: 0;
    background: hsla(0,0%,100%,.85);
    color: #1789fc;
    border: 0;
    padding: 3px 12px;
    line-height: 1;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0 5px 5px 0;
    font-family: Staatliches,Arial,Helvetica,sans-serif;
    box-shadow: 0 0 8px rgba(0,0,0,.1);
    font-size: 2em;

    &--right {
      left: auto;
      right: 0;
      border-radius: 5px 0 0 5px;
    }
  }
  &-radioBtn {
    display: none;

    &:checked + .Modal-radioLabel {
      background: #296eb4;
    }
  }
  &-radioLabel {
    padding: 8px 12px;
    margin: 0 4px;
    border-radius: 5px;
  }
  &-checkBox {
    width: 45px;
    background: #296eb4;
    padding: 0;
    -webkit-appearance: none;
    height: 28px;
    margin: 0;
    border-radius: 25px;
    position: relative;
    border: 0;

    &::after {
      content: "OFF";
      position: absolute;
      top: 0;
      left: 0;
      margin: 4px;
      border-radius: 50%;
      background: #fff;
      width: 20px;
      line-height: 20px;
      height: 20px;
      -webkit-transition: left .25s;
      transition: left .25s;
      color: #296eb4;
      font-family: Staatliches;
      text-align: center;
    }
    &:checked::after {
      content: "ON";
      left: 17px;
    }
    &:focus {
      outline: 0;
    }
  }
  &-flexWrap {
    display: flex;
    align-items: center;
    margin-top: 1em;
  }
  &-title {
    font-weight: normal;
    font-size: 2em;
    text-align: center;
    margin: 0 0 .75em;

    &:not(:first-child) {
      margin-top: 1em;
    }
  }
  &-subtitle,
  &-label {
    font-size: 1.5em;
    font-weight: normal;
    line-height: 1em;
  }
  &-subtitle {
    margin: 1em 0 0;
  }
  &-label {
    margin-right: 8px;
  }
  &-copy {
    margin: .5em 0;
    font-family: "Helvetica", "Arial", sans-serif;
  }
  &-emphasis {
    font-weight: bold;
  }
}
